import { SHOW_ALL_TASKS, ADD_TASK, EDIT_TASK, SHOW_TASK, DELETE_TASK } from './types'

import axios from 'axios'

const config = {
    headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        'Authorization': `Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..-eN6bCHhA3wq41wTyecN-LcJiDylUSxB5AuzE00yZCk`,
        'Accept': 'application / json'
    }
};

export const showAllTasks = () => async dispatch => {  //RETRIEVE

    await axios
        .get(
            "https://stage.api.sloovi.com/task/lead_6996a7dcdddc4af3b4f71ccb985cea38",
            config
        ).then((response) => {
            dispatch({
                type: SHOW_ALL_TASKS,
                payload: response.data.results
            })
        })
}

export const showProduct = id => async dispatch => {  //RETRIEVE ONE

    const result = await axios.get(`http://localhost:5000/products/${id}`)

    dispatch({
        type: SHOW_TASK,
        payload: result.data
    })
}


export const deleteTask = id => async dispatch => {  //DELETE
    await axios
        .delete(
            `https://stage.api.sloovi.com/task/lead_6996a7dcdddc4af3b4f71ccb985cea38/${id}`,
            config
        ).then(() => {
            console.log("sucesss")
            dispatch({
                type: DELETE_TASK,
                payload: id
            })
        })
}

export const editTask = newTaskData => async dispatch => {

    axios.put(
        `https://stage.api.sloovi.com/task/lead_6996a7dcdddc4af3b4f71ccb985cea38/task_b459a96f228147d68a70145e5c7c1517`,
        config
    ).then((response) => {
        console.log("sucesss")
        dispatch({
            type: EDIT_TASK,
            payload: response.data.results
        })
    })

}

export const addTask = newTaskData => async dispatch => {

    axios
        .post(
            "https://stage.api.sloovi.com/task/lead_6996a7dcdddc4af3b4f71ccb985cea38",
            JSON.stringify(newTaskData),
            config
        ).then((response) => {
            console.log(response);
            dispatch({
                type: ADD_TASK,
                payload: response.data.results
            })

        })


}

